import React, {useState,useContext, useEffect} from 'react'
import './TasksView.css'
import UserContext from "./UserContext";
import {useParams} from "react-router-dom";

function CreateTask(props) {
    const [rows, setRows] = useState([]);
    const [taskName, setTaskName] = useState('');
    const [taskCategory, setTaskCategory] = useState('');
    const [taskDesc, setTaskDesc] = useState('');
    const [taskValues, setTaskValues] = useState([]);
    const [rowId, setRowId] = useState(0);
    const {authToken}=useContext(UserContext);
    const {taskId} = useParams();

    useEffect(() => {
        if(taskId){
            fetchTaskById();
        }
    },[])

    const fetchTaskById = async function(){
        const response = await fetch(`/tasks/${taskId}`, {
            method: 'GET', 
            headers: {
            'Content-Type': 'application/json',
             authToken: authToken
            },
        });
        const taskData = await response.json();
        if(taskData.error){
            alert(taskData.error);
        }else{
            setTaskName(taskData.taskName)
            setTaskCategory(taskData.taskCategory)
            setTaskDesc(taskData.taskDescription)
            setTaskValues(taskData.taskValues);
            showValues(taskData.taskValues)
        }
    }

    const showValues = function(values){
        var i=0;
        var tableRows =[];
        for(i;i<values.length;i++){
            const key = i;
            tableRows = tableRows.concat(
                <tr>
                    <td>
                        <input name='propName' id={key} value={values[i].name} onChange={handleChange} type="text"></input>
                    </td>
                    <td>
                        <input name="propValue" id={key} value={values[i].value} onChange={handleChange} type="text" style={{width:'450px'}}></input>
                    </td>
                </tr>
            )
        }
        setRowId(i);
        setRows(tableRows);
    }
    
    const handleSave = async function(){ 
        if(taskName=="" || taskCategory=="" || taskDesc=="")
            alert('Record Name, Category and Description are mandatory');
        else{
            var task = {};
            task.taskName = taskName;
            task.taskCategory = taskCategory;
            task.taskDescription = taskDesc;
            var values = JSON.parse(JSON.stringify(taskValues));
            values = values.filter( val => (val.propName!=="" || val.propValue!==""));
            task.taskValues = values;
            var response;
            if(taskId){
                response = await fetch(`/tasks/${taskId}`, {
                    method: 'POST', 
                    headers: {
                    'Content-Type': 'application/json',
                     authToken: authToken
                    },
                    body: JSON.stringify(task)
                });
            }
            else{
                response = await fetch('/tasks', {
                    method: 'POST', 
                    headers: {
                    'Content-Type': 'application/json',
                     authToken: authToken
                    },
                    body: JSON.stringify(task)
                });
            }
    
            const data = await response.json();
            if(data.error){
                alert(data.error);
            }else{
                props.history.push("/tasks");
            }
        }
    }

    const appendRow = function(e){
        const key = rowId;
        var joined = rows.concat(
            <tr>
                <td>
                    <input name='propName' id={key} onChange={handleChange} type="text"></input>
                </td>
                <td>
                    <input name="propValue" id={key} onChange={handleChange} type="text" style={{width:'450px'}}></input>
                </td>
            </tr>
        )
        setRows(joined);
        const vals = taskValues;
        vals.push({id : key, propName : '', propValue:''});
        setTaskValues(vals);
        setRowId(key+1);
    }

    const appendRowArea = function(e){
        const key = rowId;
        var joined = rows.concat(
            <tr>
                <td>
                    <input name='propName' id={key} onChange={handleChange} type="text"></input>
                </td>
                <td>
                    <textarea name="propValue" id={key} onChange={handleChange} type="text" style={{width:'450px'}}></textarea>
                </td>
            </tr>
        )
        setRows(joined);
        const vals = taskValues;
        vals.push({id : key, propName : '', propValue:''});
        setTaskValues(vals);
        setRowId(key+1);
    }

    const handleChange = function(e){
        var val = e.target.value;
        var index = parseInt(e.target.id);
        var vals = taskValues;
        if(e.target.name=="propName")
            vals[index].propName = e.target.value;
        else if(e.target.name="propValue")
            vals[index].propValue = e.target.value;
        setTaskValues(vals);
    }

    return (
        <div className="create-tasks-page">
            <div className="row">
                <div className="col-md-3 col-lg-3 col-sm-3">
                    <label className="create-task-label">Create Record</label>
                </div>
                <div className="col-md-7 col-lg-7 col-sm-7"></div>
                <div className="col-md-2 col-sm-2 col-lg-2">
                    <button className="btn btn-primary save-button" onClick={handleSave}>Save</button>
                </div>
            </div>

            <div className="row create-task-input">
                <div className="col-md-1 col-lg-1 col-sm-1"></div>
                <div className="col-md-10 col-lg-10 col-sm-10">
                    <div className="row">
                        <div className="col-md-2 col-sm-2 col-lg-2">
                            <label>Record Name</label>
                        </div>
                        <div className="col-md-3 col-sm-3 col-lg-3">
                            <input type="text" value={taskName} onChange = { e => setTaskName(e.target.value)}></input>
                        </div>
                        <div className="col-md-7 col-sm-7 col-lg-7"></div>
                    </div>
                    <div className="row">
                        <div className="col-md-2 col-sm-2 col-lg-2">
                            <label>Record Category</label>
                        </div>
                        <div className="col-md-3 col-sm-3 col-lg-3">
                            <input value={taskCategory} onChange = { e => setTaskCategory(e.target.value)} type="text"></input>
                        </div>
                        <div className="col-md-7 col-sm-7 col-lg-7"></div>
                    </div>
                    <div className="row">
                        <div className="col-md-2 col-sm-2 col-lg-2">
                            <label>Record Description</label>
                        </div>
                        <div className="col-md-3 col-sm-3 col-lg-3">
                            <input value={taskDesc} onChange = { e => setTaskDesc(e.target.value)} type="text"></input>
                        </div>
                        <div className="col-md-7 col-sm-7 col-lg-7"></div>
                    </div>
                    <div>
                        <label>Record Values:</label>
                    </div>
                    <div className="values-table">
                        <table>
                            <thead>
                                <th style={{width: '300px'}}>Property Name</th>
                                <th style={{width: '600px'}}>Property Value</th>
                            </thead>
                            <tbody>
                                {rows}
                            </tbody>
                        </table>
                        <button className="btn btn-primary add-button" onClick={appendRow}>Add Row with TextBox</button>
                        <button className="btn btn-primary add-area" onClick={appendRowArea}>Add Row with TextArea</button>
                    </div>   
                </div>
                <div className="col-md-1 col-sm-1 col-lg-1"></div>
            </div>
        </div>
    )
}

export default CreateTask
